<template>
  <div class="mb-5">
    <Input v-model="data.amount" type="text" @keypress="filterDecimal" label="Amount" class="w-full" :currency="true" :disabled="disabled" />
  </div>
  <div class="mb-5">
    <Input v-model="data.note" type="textarea" :label="isReturn && 'Reason' || 'Notes'" class="w-full" rows="6" :disabled="disabled" />
  </div>
</template>

<script>
import { defineComponent, reactive, watch } from 'vue'
import { filterDecimal } from 'utils/number'
import Input from 'bbUi/components/input/input.vue'

export default defineComponent({
  components: {
    Input
  },
  props: {
    data: {
      type: Object,
      default: () => ({
        amount: 0,
        maxAmount: 0,
        note: null
      })
    },
    disabled: {
      type: Boolean,
      default: false
    },
    isReturn: {
      type: Boolean,
      default: false
    }
  },
  emits: [
    'update:data'
  ],
  setup(props, { emit }) {
    const data = reactive(props.data)

    watch(() => ({...data}), () => {
      if( props.data.maxAmount ) data.amount = data.amount > data.maxAmount && data.maxAmount || data.amount
      emit('update:data', data)
    })

    const filterNumber = ($event) => {
      const keyCode = ($event.keyCode ? $event.keyCode : $event.which)

      if (keyCode < 48 || keyCode > 57) {
        $event.preventDefault()
      }
    }

    return {
      data,
      filterNumber,
      filterDecimal
    }
  }
})
</script>